@import '../../assets/css/mixins';

.iconPoolAdd {
  position: relative;
  padding: 4px;
  width: 38px;
  height: 38px;
  border-radius: 50%;
  background: #1b0d31;
  .inner {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background: #26193d;
    color: #524b71;
    cursor: pointer;
    &:hover {
      color: #fff;
    }
  }
  .icon {
    display: block;
    width: 16px;
    height: 16px;
  }
  .reverse {
    color: #fff;
    &:hover {
      background: #6e0dff;
    }
    &.disabled {
      background: #26193d !important;
      color: #524b71 !important;
    }
  }
  .disabled {
    cursor: default;
    color: #524b71;
    &:hover {
      color: #524b71;
    }
  }
}

@include mobile-only {
  .iconPoolAdd {
    padding: 4px;
    width: 37px;
    height: 37px;
    .icon {
      width: 15px;
      height: 15px;
    }
  }
}
