@import '../../../../assets/css/theme';
@import '../../../../assets/css/mixins';

@mixin greyText {
  font-family: Poppins;
  font-weight: 400;
  font-size: 12px;
  line-height: 21px;
}

.v2-order-modal.ant-modal {
  .ant-modal-content {
    border-radius: 10px;
  }
  .ant-modal-footer {
    border: none;
    text-align: center;
    .ant-btn {
      height: 46px;
    }
  }
}

.v2-order-modal-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  line-height: 1em;
  padding: 10px 0;
  color: #333;
}

.v2-order {
  &-title {
    font-size: 12px;
  }
  &-grey-text {
    @include greyText;
    margin: 10px 0 20px;
    color: #9e9abc;
  }
  &-info {
    background: $color-info-bg-light;
    border-radius: 5px;
    border: 1px solid rgba($color-text-default, 0.2);
    padding: 10px;
    font-weight: 600;
    color: $light-purple-btn;
    line-height: 24px;
    text-align: left;
  }
  &-logo {
    width: 16px;
    height: 16px;
    margin-right: 6px;
    vertical-align: middle;
    border-radius: 50%;
    border: 1px solid #ddd;
    background: #fff;
  }
  &-ver-mid {
    vertical-align: middle;
    color: #666;
  }
}

//v2-order
.v2-order {
  &-success {
    text-align: center;
  }
  &-success-icon {
    width: 80px;
    margin-top: 20px;
  }
  &-title-green {
    font-size: 20px;
    color: #16c378;
    margin: 24px 0 12px;
  }
  &-flex {
    display: flex;
    justify-content: center;
    align-items: flex-end;
  }
  &-add-value {
    font-size: 20px;
    color: #333333;
    letter-spacing: 0;
    text-align: right;
    display: inline-block;
    word-break: break-all;
  }
  &-add-token {
    font-size: 12px;
    color: #999;
    margin-top: 9px;
    margin-left: 3px;
  }
  &-plus {
    font-size: 14px;
    color: #5915e1;
    margin: 0 3px;
  }
  &-supplyDescTitle {
    margin-top: 14px;
    opacity: 0.8;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #999999;
  }

  &-amount {
    &-center {
      text-align: center;
    }
    &-num {
      font-family: Poppins-Bold;
      font-weight: 700;
      font-size: 24px;
      line-height: 36px;
    }
    &-name {
      font-family: Poppins;
      font-weight: 400;
      font-size: 14px;
      line-height: 21px;
    }
  }

  &-line {
    text-align: center;

    &-add-value {
      font-family: Poppins-Bold;
      margin-right: 6px;
      font-size: 24px;
      color: #fff;
    }
    &-add-token {
      display: inline-block;
      font-size: 14px;
      font-family: Poppins-Bold;
      font-weight: 700;
      color: #fff;
      line-height: 17px;
    }
    &-plus {
      margin: 0 10px;
      font-weight: 700;
      font-size: 20px;
      color: rgba(160, 156, 172, 1);
    }
    &-tip {
      font-family: Poppins;
      margin: 6px 0 20px;
      font-weight: 400;
      font-size: 14px;
      line-height: 21px;
      color: #a7a2c9;
    }
  }
}
.errorMsg {
  font-size: 14px;
  color: $color-warning;
  background: url(../../assets/images/failed.svg) left no-repeat;
  background-size: 13px;
  padding-left: 18px;
  display: inline-block;
  margin: 0 auto 10px;
}

.v2-price-list {
  background: #33254e;
  border-radius: 6px;
  padding: 0 15px;
  margin-bottom: 10px;
  margin-top: 10px;
  border: 0;
  &-item {
    display: flex;
    align-items: center;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    padding: 15px 0;
    font-size: 16px;
    font-family: Poppins-Bold;
    font-weight: 600;
    color: #fff;
    line-height: 20px;
    &:last-child {
      border: none;
    }
  }
  &-left {
    display: flex;
    align-items: center;
    font-size: 16px;
    font-family: Poppins;
    font-weight: 600;
    line-height: 20px;
    > img {
      width: 20px;
      height: 20px;
      margin-right: 6px;
      border-radius: 50%;
      border: 1px solid #ddd;
      background: #fff;
    }
  }
  &-right {
    font-size: 14px;
    margin-left: auto;
    padding-left: 10px;
    font-family: Poppins;
    font-weight: 400;
  }
}

.v2-share-container {
  @include greyText;
  margin-bottom: 10px;
  color: #9e9abc;
  .v2-share-num {
    color: $color-text-default;
  }
}

@include mobile-only {
  .v2-price-list {
    padding: 0 10px;
    &-item {
      display: block;
      padding: 10px 0;
    }
    &-left {
      line-height: 21px;
      font-size: 14px;
      img {
        width: 15px;
        height: 15px;
      }
    }
    &-right {
      padding-left: 0;
      margin-top: 6px;
      font-size: 14px;
      line-height: 21px;
      color: #9e9abc;
    }
  }
}
