@import '../../../../../assets/css/mixins';

.v3-remove-modal {
  .v3-remove-modal-title {
    margin-bottom: 20px;
    font-family: Poppins;
    color: #fff;
    font-size: 14px;
    line-height: 36px;
    .v3-remove-modal-title-inner {
      & > span {
        &:nth-child(2) {
          display: inline-block;
        }
      }
    }
  }
  .v3-remove-modal-title-value {
    font-family: Poppins-Bold;
    font-weight: 700;
    font-size: 24px;
  }
  .v3-remove-modal-title-plus {
    font-family: Poppins-Bold;
    margin: 0 10px;
    font-size: 20px;
    color: rgba(255, 255, 255, 0.5);
  }
  .v3-remove-modal-title-symbol {
    font-family: Poppins-Bold;
    position: relative;
    top: -1px;
  }

  .v3-remove-modal-price-box {
    .v3-remove-modal-price-inner {
      padding: 20px;
      background: #33254e;
      border-radius: 6px;
    }
    .v3-remove-modal-price-t {
      display: flex;
      align-items: center;
      img {
        display: block;
        width: 30px;
        height: 30px;
        border-radius: 100%;
        background: #fff;
        border: 1px solid #ddd;
        &:nth-child(2) {
          margin-left: -6px;
        }
      }
      span {
        margin-left: 10px;
        font-family: Poppins-Bold;
        font-weight: 700;
        font-size: 20px;
        line-height: 30px;
      }
    }
    .v3-remove-modal-price-b {
      margin-top: 20px;
      text-align: left;
      & > div {
        margin-bottom: 10px;
        font-family: Poppins;
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        color: #fff;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }

  .v3-remove-modal-price-btn {
    margin-top: 30px;
  }

  .price-box-lp {
    margin-top: 10px;
    font-family: Poppins;
    font-size: 14px;
    line-height: 20px;
    text-align: left;
    color: #9e9abc;
    .price-box-lp-sub {
      color: #fff;
    }
  }

  .errorMsg {
    width: 100%;
    margin-top: 10px;
    margin-bottom: 0;
    text-align: left;
  }

  // success
  .v3-modal-success {
    .v3-modal-success-icon {
      display: block;
      width: 80px;
      height: 80px;
      margin: 0 auto 20px;
    }
    .endTip {
      margin-top: 10px;
      font-family: Poppins;
      font-weight: 400;
      font-size: 12px;
      line-height: 18px;
      color: #9e9abc;
      text-align: left;
    }
  }
}

@include mobile-only {
  .v3-remove-modal {
    .v3-remove-modal-title {
      margin-bottom: 16px;
      font-size: 12px;
      line-height: 30px;
    }
    .v3-remove-modal-title-value {
      font-size: 20px;
    }
    .v3-remove-modal-title-plus {
      display: block;
      margin: 0 auto 5px;
      font-size: 20px;
      line-height: 20px;
    }
    .v3-remove-modal-title-symbol {
      top: -1px;
    }

    .v3-remove-modal-price-box {
      .v3-remove-modal-price-inner {
        padding: 10px;
      }
      .v3-remove-modal-price-t {
        img {
          width: 20px;
          height: 20px;
        }
        span {
          font-size: 16px;
          line-height: 20px;
        }
      }
      .v3-remove-modal-price-b {
        margin-top: 20px;
        & > div {
          margin-bottom: 10px;
          &:last-child {
            margin-bottom: 0;
          }
        }
      }
      .v3-remove-modal-price-l {
        span {
          font-size: 16px;
        }
      }
    }

    // success
    .v3-modal-success {
      .v3-modal-success-icon {
        width: 60px;
        height: 60px;
      }
    }

    .v3-remove-modal-price-btn {
      margin-top: 26px;
    }

    .price-box-lp {
      margin-top: 10px;
      font-family: Poppins;
      font-size: 12px;
      line-height: 18px;
    }
  }
}
