@import '../../../../assets/css/theme';
@import '../../assets/css/inputBlock.scss';

/* v1v2 */
.pool_info_title {
  margin-bottom: 20px;
  font-family: Poppins;
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
  color: #fff;
}

.pool_logo {
  img {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 1px solid #ddd;
    background: #fff;
  }
  span {
    font-size: 14px;
    font-weight: 600;
    color: $color-text-default;
  }
}
.pool_tip {
  margin-top: 30px;
}

@include mobile-only {
  .pool_info_title {
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 12px;
  }
  .pool_tip {
    margin-top: 20px;
  }
}
