@import '../../../../assets/css/theme';
@import '../../../../assets/css/var.scss';
@import '../../../../assets/css/mixins';

.token_view_icon {
  cursor: pointer;
  vertical-align: -6px;
}

.token_logo_icon {
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  font-size: 16px;
  font-family: Poppins;
  font-weight: 600;
  color: $color-text-default;
  margin-right: 10px;
  img {
    display: block;
    width: 24px;
    height: 24px;
    margin: 0 6px 0 0;
    border-radius: 50%;
    border: 1px solid #ddd;
    background: #fff;
  }
}

@include mobile-only {
  .token_logo_icon {
    margin-right: 6px;
    font-size: 14px;
    img {
      width: 20px;
      height: 20px;
    }
  }
}
