.warning_tip {
  font-family: Poppins;
  background: rgba(255, 172, 0, 0.1);
  border-radius: 10px;
  padding: 10px 20px;
  &_title {
    font-family: Poppins;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: #ff8700;
    margin-bottom: 10px;
  }
  &_content {
    font-size: 14px;
    font-weight: 400;
    color: rgba(255, 135, 0, 0.5);
    line-height: 20px;
  }
}

.warning_tip_pool {
  padding: 0;
  background: transparent;
  border-radius: 0;
}
