@import '../../../../assets/css/theme';
@import '../../../../assets/css/var.scss';

.title {
  margin-bottom: 16px;
}
.title_flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.remove_liq_title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  // width: 100%;
  color: #fff;
  .remove_liq_title_l {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 20px;
    line-height: 30px;
    font-family: Poppins;
    font-weight: 600;
    color: #fff;
    a {
      color: #6c6686;
      &:hover {
        color: #fff;
      }
    }
    img {
      width: 24px;
      height: 24px;
    }
  }
  .remove_liq_title_r {
    font-family: Poppins;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: #a29cbc;
    .remove_liq_title_r_value {
      margin-left: 4px;
      font-family: Poppins-Bold;
      color: #fff;
    }
  }
}

.remove_liq_fee {
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  color: $color-text-default;
  > span:nth-child(1) {
    font-size: 10px;
    opacity: 0.5;
  }
  > span:nth-child(2) {
    font-size: 14px;
  }
}

.remove_liq_num {
  font-size: 50px;
  font-family: Poppins-Bold;
  font-weight: bold;
  color: $color-text-default;
  margin-top: 10px;
}

.remove_liq_token {
  font-size: 14px;
  font-family: Poppins;
  font-weight: 600;
  color: $color-text-default;
  > img {
    width: 20px;
    height: 20px;
    margin-right: 6px;
    border-radius: 50%;
    border: 1px solid #ddd;
    background: #fff;
  }
}

.remove_liq_value {
  font-size: 16px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  color: $color-text-default;
  text-align: center;
}

.remove_liq_price {
  width: fill-available;
  width: -webkit-fill-available;
  margin: 0 25px;
  text-align: center;
  > p {
    position: relative;
    font-size: 12px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $color-text-default;
    opacity: 0.3;
    opacity: 0.3;
    &:nth-child(1) {
      border-bottom: 1px solid $color-text-default;

      > span {
        position: absolute;
        right: -6px;
        bottom: 7px;
        width: 10px;
        height: 10px;
        border-left: 1px solid $color-text-default;
        transform: rotate(-45deg);
        transform-origin: right bottom;
      }
    }
    &:nth-child(2) {
      border-top: 1px solid $color-text-default;
      margin-top: 7px;
      > span {
        position: absolute;
        left: 0px;
        top: 0px;
        left: 0;
        top: 0;
        width: 10px;
        height: 10px;
        border-left: 1px solid $color-text-default;
        transform: rotate(-45deg);
        transform-origin: left top;
      }
    }
  }
}

.remove_text_color {
  color: $color-text-default;
}
.feeSelector {
  display: flex;
  align-items: center;
  font-family: Poppins;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  margin-top: 10px;
  .feeSelector_label {
    margin-right: 4px;
    white-space: nowrap;
    color: #a29cbc;
  }
  .feeSelector_value {
    color: #ffffff;
  }
}

.token_info {
  margin-top: 30px;
  .token_info_item {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    color: #fff;
    &:last-child {
      margin-bottom: 0;
    }
    .token_info_item_l {
      display: flex;
      align-items: center;
      flex-wrap: nowrap;
      &:last-child {
        margin-bottom: 0;
      }
      img {
        display: block;
        margin-right: 10px;
        width: 24px;
        height: 24px;
        border-radius: 100%;
        background: #fff;
      }
      span {
        font-family: Poppins;
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
        white-space: nowrap;
      }
    }
    .token_info_item_r {
      margin-left: auto;
      font-family: Poppins-Bold;
      font-weight: 700;
      font-size: 20px;
    }
  }
}

.price_list {
  margin-top: 20px;
  .price_list_item_inner {
    display: inline-flex;
    align-items: center;
    flex-wrap: wrap;
  }
  .price_list_item {
    margin-bottom: 10px;
    font-family: Poppins;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: #a29cbc;
    &:last-child {
      margin-bottom: 0;
    }
    img {
      display: block;
      margin-right: 4px;
      width: 14px;
      height: 14px;
      border-radius: 100%;
      border: 1px solid #ddd;
      background: #fff;
    }
    .price_list_item_value {
      font-family: Poppins-Bold;
      font-weight: 700;
      color: #fff;
      word-break: break-all;
    }
  }
}

.remove_liq_btn_list {
  display: flex;
  margin-top: 26px;
  .remove_liq_btn {
    flex: 1;
    padding: 10px 5px;
    line-height: 21px;
    margin-right: 10px;
    background: #33254e;
    border-radius: 5px;
    border: 0;
    font-size: 14px;
    font-family: Poppins;
    font-weight: 600;
    color: #a7a2c9 !important;
    cursor: pointer;
    &:last-child {
      margin-right: 0;
    }
  }

  .remove_liq_active_btn {
    background: #6400ff;
    border-radius: 5px;
    font-size: 14px;
    font-family: Poppins;
    font-weight: 600;
    color: #fff !important;
  }
}

.slider_num {
  margin-bottom: 12px;
  font-size: 50px;
  line-height: 60px;
  font-family: Poppins-Bold;
  font-weight: 700;
  color: $color-text-default;
}

.slider_wrap {
  margin-top: 20px;
}

.submit_btn {
  margin-top: 30px;
}

@include mobile-only {
  .title {
    margin-bottom: 10px;
  }

  .remove_liq_title {
    .remove_liq_title_l {
      font-size: 16px;
      line-height: 24px;
    }
    .remove_liq_title_r {
      font-size: 12px;
      line-height: 18px;
    }
    .remove_liq_title_setting {
      display: none;
    }
  }

  .token_info {
    margin-top: 20px;
    .token_info_item {
      margin-bottom: 12px;
      &:last-child {
        margin-bottom: 0;
      }
      .token_info_item_l {
        &:last-child {
          margin-bottom: 0;
        }
        img {
          margin-right: 6px;
          width: 20px;
          height: 20px;
        }
        span {
          font-size: 14px;
          line-height: 20px;
        }
      }
      .token_info_item_r {
        font-size: 16px;
      }
    }
  }

  .price_list {
    margin-top: 10px;
  }

  .remove_liq_btn_list {
    margin-top: 14px;
    .remove_liq_btn {
      padding: 5px;
      margin-right: 5px;
      border-radius: 5px;
      font-size: 14px;
      // line-height: 21px;
      &:last-child {
        margin-right: 0;
      }
    }
  }

  .slider_wrap {
    margin-top: 10px;
  }
  .slider_num {
    margin-bottom: 7px;
    font-size: 30px;
    line-height: 44px;
  }

  .submit_btn {
    margin-top: 20px;
  }
}
